function sliderNews() {
    var swiper = new Swiper('.news .swiper', {
        slidesPerView: 2,
        spaceBetween: 30,
        pagination: {
            el: ".news .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 15,
            },
            501: {
                slidesPerView: 1.5,
                spaceBetween: 15,
            },
            769: {
                slidesPerView: 2,
                spaceBetween: 30,
            }
        }
    })
}

function sliderPartners() {
    var swiper = new Swiper('.partners .swiper', {
        slidesPerView: 5,
        spaceBetween: 30,
        navigation: {
            nextEl: '.partners .swiper-button-next',
            prevEl: '.partners .swiper-button-prev',
        },
        pagination: {
            el: ".partners .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 3,
                spaceBetween: 15,
            },
            501: {
                slidesPerView: 4,
                spaceBetween: 15,
            },
            577: {
                slidesPerView: 5,
                spaceBetween: 30,
            }
        }
    })
}

function sliderFeedback() {
    var swiper = new Swiper('.feedback .swiper', {
        slidesPerView: 3,
        spaceBetween: 15,
        pagination: {
            el: ".feedback .swiper-pagination",
            clickable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
            },
            577: {
                slidesPerView: 2,
            },
            993: {
                slidesPerView: 3,
            }
        }
    })
}

$(document).ready(function() {
    sliderNews();
    sliderPartners();
    sliderFeedback();


//Menu
$('.burger').click(function () {
    $(this).toggleClass('burger--active')
    $('.header-bottom').toggleClass('header-bottom--active')
    $('body').toggleClass('fixed-body')
})

//Tabs
    $(".city-tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".city-tab").removeClass("city-tab--active")
        $(this).addClass("city-tab--active")

        $(this).parents(".page-frame").find(".city-tab__content").removeClass("city-tab__content--active")
        $(this).parents(".page-frame").find(`.city-tab__content[data-content-path="${path}"]`).addClass("city-tab__content--active")
    })

// Select
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list li').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list li").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list li')
    let selectWrap = $('.select__wrap')

    $(document).click(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

    //Accordion
    $(".visas__item-show").click(function () {
        let parent = $(this).parents(".visas__item")
        if($(parent).hasClass("visas__item--active")){
            $(".visas__item").removeClass("visas__item--active")
            $(".visas__item-hidden").slideUp()
        }else{
            $(".visas__item").removeClass("visas__item--active")
            $(parent).addClass("visas__item--active")
            $(".visas__item-hidden").slideUp()
            $(this).siblings(".visas__item-hidden").slideDown()
        }
    })


})




